export default class Plugin {
}

Plugin.priority = Infinity;

if (typeof window !== 'undefined') {
  if (window.poincare == null)
    window.poincare = {};
  if (window.poincare.plugins == null)
    window.poincare.plugins = {};
  window.poincare.plugins.Plugin = Plugin;
}



